// Configuration
@import "../../../default/bootstrap/scss/functions";
@import "../../../default/bootstrap/scss/variables";
@import "../../../default/bootstrap/scss/mixins";

// Create your own color map
$custom-colors: (
  "tertiary": $indigo,
  "quaternary": $purple,
  "quinary": $pink,
  "senary": $orange,
  "septenary": $teal
);

// Merge with "$theme-colors" map
$theme-colors: map-merge($theme-colors, $custom-colors);

